html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%; }

body {
  margin: 0; }

main {
  display: block; }

h1 {
  font-size: 2em;
  margin: .67em 0; }

hr {
  overflow: visible;
  box-sizing: content-box;
  height: 0; }

pre {
  font-family: monospace, monospace;
  font-size: 1em; }

a {
  background-color: transparent; }

abbr[title] {
  text-decoration: underline;
  text-decoration: underline dotted;
  border-bottom: none; }

b,
strong {
  font-weight: bolder; }

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em; }

small {
  font-size: 80%; }

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sub {
  bottom: -.25em; }

sup {
  top: -.5em; }

img {
  border-style: none; }

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0; }

button,
input {
  overflow: visible; }

button,
select {
  text-transform: none; }

button,
[type='button'],
[type='reset'],
[type='submit'] {
  -webkit-appearance: button; }

button::-moz-focus-inner,
[type='button']::-moz-focus-inner,
[type='reset']::-moz-focus-inner,
[type='submit']::-moz-focus-inner {
  padding: 0;
  border-style: none; }

button:-moz-focusring,
[type='button']:-moz-focusring,
[type='reset']:-moz-focusring,
[type='submit']:-moz-focusring {
  outline: 1px dotted ButtonText; }

fieldset {
  padding: .35em .75em .625em; }

legend {
  display: table;
  box-sizing: border-box;
  max-width: 100%;
  padding: 0;
  white-space: normal;
  color: inherit; }

progress {
  vertical-align: baseline; }

textarea {
  overflow: auto; }

[type='checkbox'],
[type='radio'] {
  box-sizing: border-box;
  padding: 0; }

[type='number']::-webkit-inner-spin-button,
[type='number']::-webkit-outer-spin-button {
  height: auto; }

[type='search'] {
  outline-offset: -2px;
  -webkit-appearance: textfield; }

[type='search']::-webkit-search-decoration {
  -webkit-appearance: none; }

::-webkit-file-upload-button {
  font: inherit;
  -webkit-appearance: button; }

details {
  display: block; }

summary {
  display: list-item; }

template {
  display: none; }

[hidden] {
  display: none; }

@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: normal;
  src: url("../../fonts/Lato-Regular.woff2") format("woff2"), url("../../fonts/Lato-Regular.woff") format("woff"), url("../../fonts/Lato-Regular.ttf") format("truetype"); }

@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 300;
  src: url("../../fonts/Lato-Light.woff2") format("woff2"), url("../../fonts/Lato-Light.woff") format("woff"), url("../../fonts/Lato-Light.ttf") format("truetype"); }

@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: bold;
  src: url("../../fonts/Lato-Bold.woff2") format("woff2"), url("../../fonts/Lato-Bold.woff") format("woff"), url("../../fonts/Lato-Bold.ttf") format("truetype"); }

@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 900;
  src: url("../../fonts/Lato-Black.woff2") format("woff2"), url("../../fonts/Lato-Black.woff") format("woff"), url("../../fonts/Lato-Black.ttf") format("truetype"); }

* {
  box-sizing: border-box; }

body {
  font-family: 'Lato', sans-serif; }

a {
  text-decoration: none; }

p {
  text-align: justify; }

.page-container {
  max-width: 1020px;
  margin: 0 auto; }

.header {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  padding: 34px 40px 35px;
  background-color: #2d303a;
  border-bottom: 6px solid #323746; }

.logo {
  display: flex;
  font-size: 25px;
  line-height: 20px;
  font-weight: bold;
  color: #ffffff; }
  .logo__upper {
    color: #f06c64; }

.navigation {
  display: flex; }
  .navigation__item {
    position: relative;
    margin-right: 26px;
    font-size: 12.5px;
    line-height: 9px;
    font-weight: bold;
    text-transform: uppercase;
    color: #ffffff; }
    .navigation__item::after {
      content: '.';
      position: absolute;
      right: -15px;
      top: -30%;
      color: #494e62;
      pointer-events: none; }
    .navigation__item:hover, .navigation__item:focus {
      color: #f06c64; }
      .navigation__item:hover::after, .navigation__item:focus::after {
        color: #494e62; }
    .navigation__item:last-child {
      margin-right: 0; }
      .navigation__item:last-child::after {
        display: none; }

.main {
  height: 600px;
  padding: 0 40px;
  background-color: #f06c64;
  border-bottom: 6px solid #ea676b;
  position: relative; }
  .main__first-image {
    position: absolute;
    top: 53px;
    left: 112px; }
  .main__second-image {
    position: absolute;
    top: 174px;
    right: 111px; }
  .main__arrow {
    position: absolute;
    top: calc(50% - 27px);
    cursor: pointer;
    transition: transform .2s; }
    .main__arrow--left {
      left: 40px; }
      .main__arrow--left:hover {
        transform: scale(1.2); }
    .main__arrow--right {
      transform: rotate(180deg);
      right: 40px; }
      .main__arrow--right:hover, .main__arrow--right:focus {
        transform: rotate(180deg) scale(1.2); }

.section {
  padding: 67px 40px 0;
  background-color: #f2f2f2;
  border-bottom: 6px solid #ffffff; }
  .section__header {
    margin-top: 0;
    margin-bottom: 26px; }
  .section__text {
    margin-top: 0;
    margin-bottom: 47px; }

.h1 {
  font-size: 30px;
  font-weight: 900;
  line-height: 20px;
  color: #666d89; }

.text {
  font-size: 18px;
  font-weight: 300;
  line-height: 1.67;
  color: #767e9e; }

.card-container {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 30px; }

.card {
  display: flex;
  flex: 1 1 30%;
  margin-right: 20px;
  margin-bottom: 32px; }
  .card:nth-child(3n) {
    margin-right: 0; }
  .card__title {
    margin-top: 0;
    margin-bottom: 10px;
    font-size: 18px;
    line-height: 1;
    font-weight: 900;
    color: #666d89; }
  .card__text {
    margin: 0;
    font-size: 12px;
    line-height: 1.83;
    color: #767e9e; }
  .card__icon {
    margin-right: 21px; }
