html {
	line-height: 1.15;
	-webkit-text-size-adjust: 100%; }

body {
	margin: 0; }

main {
	display: block; }

h1 {
	font-size: 2em;
	margin: .67em 0; }

hr {
	overflow: visible;
	box-sizing: content-box;
	height: 0; }

pre {
	font-family: monospace, monospace;
	font-size: 1em; }

a {
	background-color: transparent; }

abbr[title] {
	text-decoration: underline;
	text-decoration: underline dotted;
	border-bottom: none; }

b,
strong {
	font-weight: bolder; }

code,
kbd,
samp {
	font-family: monospace, monospace;
	font-size: 1em; }

small {
	font-size: 80%; }

sub,
sup {
	font-size: 75%;
	line-height: 0;
	position: relative;
	vertical-align: baseline; }

sub {
	bottom: -.25em; }

sup {
	top: -.5em; }

img {
	border-style: none; }

button,
input,
optgroup,
select,
textarea {
	font-family: inherit;
	font-size: 100%;
	line-height: 1.15;
	margin: 0; }

button,
input {
	overflow: visible; }

button,
select {
	text-transform: none; }

button,
[type='button'],
[type='reset'],
[type='submit'] {
	-webkit-appearance: button; }

button::-moz-focus-inner,
[type='button']::-moz-focus-inner,
[type='reset']::-moz-focus-inner,
[type='submit']::-moz-focus-inner {
	padding: 0;
	border-style: none; }

button:-moz-focusring,
[type='button']:-moz-focusring,
[type='reset']:-moz-focusring,
[type='submit']:-moz-focusring {
	outline: 1px dotted ButtonText; }

fieldset {
	padding: .35em .75em .625em; }

legend {
	display: table;
	box-sizing: border-box;
	max-width: 100%;
	padding: 0;
	white-space: normal;
	color: inherit; }

progress {
	vertical-align: baseline; }

textarea {
	overflow: auto; }

[type='checkbox'],
[type='radio'] {
	box-sizing: border-box;
	padding: 0; }

[type='number']::-webkit-inner-spin-button,
[type='number']::-webkit-outer-spin-button {
	height: auto; }

[type='search'] {
	outline-offset: -2px;

	-webkit-appearance: textfield; }

[type='search']::-webkit-search-decoration {
	-webkit-appearance: none; }

::-webkit-file-upload-button {
	font: inherit;
	-webkit-appearance: button; }

details {
	display: block; }

summary {
	display: list-item; }

template {
	display: none; }

[hidden] {
	display: none; }
